import helper from '../../common/helper.js';
export default {
	data() {
		return {
			userfoArray:'',
			num: '13',        //默认显示9条任务数据
			tishi:''
		};
	},
	onLoad() {
		this.srlist()
	},
	onPullDownRefresh: function() { //下拉刷新
		this.srlist(true);
	},
	//触底事件
	onReachBottom() {                     
		var tas1 = new String('5');         //触底加载5条数据
		this.num = Number(this.num) + Number(tas1);
		this.srlist();
	},
	methods: {
		srlist() {
			uni.showLoading({
				title: "加载中.....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + '/withdrawals/getlist',
				method: 'GET',
				data: helper.postdata({
					"userid": helper.getstate().userid,
					"page": "1",
					"num":  this.num,
				}),
				success: res => {
					if (res.data.code == 200) {
						uni.hideLoading();
						uni.stopPullDownRefresh(); //刷新停止
						this.userfoArray = res.data.data.baseinfo;
						if (this.userfoArray=="") {
							this.tishi = '暂无提现记录'
						} else{
							this.tishi = ''
						}
					}else{
						uni.showToast({
							icon: 'none',
							title: '网络异常,请下拉刷新重试！'
						});
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请下拉刷新重试！'
					});
				},
			});

		}
	}
}
